import { PrjInfoContainer } from './ProjectInfoStyled';
function ProjectInfo({ challenge }) {
	switch (
		challenge.challengeTargetCode //1: , 2:   , 3:  /, 4: 
	) {
		case 1: {
			//
			return (
				<PrjInfoContainer>
					<table>
						<tbody>
							<tr>
								<th className="prjinfo_header"></th>
								<td>{challenge.ownerName ? challenge.ownerName : 'no data'}</td>
								<th></th>
								<td>{challenge.companyA ? challenge.companyA : 'no data'}</td>
							</tr>
							<tr>
								{' '}
								<th> / </th>
								<td>{challenge.companyB ? challenge.companyB : 'no data'}</td>
								<th></th>
								<td>
									{challenge.url ? (
										<a href={challenge.url} target={'_blank'}>
											{challenge.url}
										</a>
									) : (
										'no data'
									)}
								</td>
							</tr>
							<tr>
								<th> </th>
								<td>
									{(() => {
										let arr = challenge.rewards.map((el) => {
											switch (el.cat) {
												case 0: {
													return ' : ' + el.pts;
												}
												case 1: {
													return '';
												}
												case 2: {
													return '';
												}
												case 3: {
													return '';
												}
												case 4: {
													return ' ';
												}
												case 5: {
													return '';
												}
												case 6: {
													return ' ';
												}
												case 7: {
													return '';
												}
												case 99: {
													return el.rewarddesc;
												}
												default: {
													return;
												}
											}
										});
										return arr.join(', ');
									})()}
								</td>
								<th></th>
								<td>
									{challenge.chargeContactShown
										? challenge.chargeContact
										: ''}
								</td>
							</tr>
							<tr>
								<th></th>
								<td>{challenge.chargeShown ? challenge.charge : ''}</td>
								<th></th>
								<td>
									{challenge.chargeeMailShown
										? challenge.chargeeMail
										: ''}
								</td>
							</tr>
						</tbody>
					</table>
				</PrjInfoContainer>
			);
		}
		case 2: {
			// 
			return (
				<PrjInfoContainer>
					<table>
						<tbody>
							<tr>
								<th className="prjinfo_header"></th>
								<td>  </td>
								<th> </th>
								<td>{challenge.companyA ? challenge.companyA : 'no data'}</td>
							</tr>
							<tr>
								<th></th>
								<td>
									{(() => {
										switch (challenge.meetCode) {
											case 1: {
												return ' ';
											}
											case 2: {
												return ' ';
											}
										}
									})()}
								</td>
								<th></th>
								<td>
									{(() => {
										let arr = challenge.rewards.map((el) => {
											if (el.cat === 0) {
												return `${el.pts} ${el.currency.toUpperCase()}`;
											} else if (el.cat === 99) {
												return el.rewardDesc;
											}
										});
										return arr.join(', ');
									})()}
								</td>
							</tr>
							<tr>
								<th></th>
								<td>{challenge.chargeShown ? challenge.charge : ''}</td>

								<th></th>
								<td>
									{challenge.chargeContactShown
										? challenge.chargeContact
										: ''}
								</td>
							</tr>
							<tr>
								<th></th>
								<td>
									{challenge.chargeeMailShown
										? challenge.chargeeMail
										: ''}
								</td>
							</tr>
						</tbody>
					</table>
				</PrjInfoContainer>
			);
		}
		case 3: {
			//  / 
			return (
				<PrjInfoContainer>
					<table>
						<tbody>
							<tr>
								<th className="prjinfo_header"></th>
								<td>' / </td>
								<th> </th>
								<td>{challenge.companyA ? challenge.companyA : 'no data'}</td>
							</tr>
							<tr>
								<th></th>
								<td>
									{(() => {
										switch (challenge.meetCode) {
											case 1: {
												return ' ';
											}
											case 2: {
												return ' ';
											}
										}
									})()}
								</td>
								<th></th>
								<td>
									{(() => {
										let arr = challenge.rewards.map((el) => {
											if (el.cat === 0) {
												return `${el.pts} ${el.currency.toUpperCase()}`;
											} else if (el.cat === 99) {
												return el.rewardDesc;
											}
										});
										return arr.join(', ');
									})()}
								</td>
							</tr>
							<tr>
								<th></th>
								<td>{challenge.chargeShown ? challenge.charge : ''}</td>

								<th></th>
								<td>
									{challenge.chargeContactShown
										? challenge.chargeContact
										: ''}
								</td>
							</tr>
							<tr>
								<th></th>
								<td>
									{challenge.chargeeMailShown
										? challenge.chargeeMail
										: ''}
								</td>
							</tr>
						</tbody>
					</table>
				</PrjInfoContainer>
			);
		}
		case 4: {
			//
			return (
				<PrjInfoContainer>
					<table>
						<tbody>
							<tr>
								<th className="prjinfo_header"></th>
								<td> </td>
								<th> </th>
								<td>{challenge.companyA ? challenge.companyA : 'no data'}</td>
							</tr>
							<tr>
								<th></th>
								<td>
									{(() => {
										switch (challenge.hire.isOnline) {
											case 1: {
												return ' ';
											}
											case 2: {
												return ' ';
											}
										}
									})()}
								</td>
								<th></th>
								<td>
									{(() => {
										let arr = challenge.rewards.map((el) => {
											if (el.cat === 0) {
												return `${el.pts} ${el.currency.toUpperCase()}`;
											} else if (el.cat === 99) {
												return el.rewardDesc;
											}
										});
										return arr.join(', ');
									})()}
								</td>
							</tr>
							<tr>
								<th></th>
								<td>{challenge.hire.loc ? challenge.hire.loc : 'no data'}</td>
								<th></th>
								<td>, </td>
							</tr>
							<tr>
								<th></th>
								<td>{challenge.chargeShown ? challenge.charge : ''}</td>

								<th></th>
								<td>
									{challenge.chargeContactShown
										? challenge.chargeContact
										: ''}
								</td>
							</tr>
							<tr>
								<th></th>
								<td>
									{challenge.chargeeMailShown
										? challenge.chargeeMail
										: ''}
								</td>
							</tr>
						</tbody>
					</table>
				</PrjInfoContainer>
			);
		}
		default: {
			return <div>no data</div>;
		}
	}
}
export default ProjectInfo;
